.testimonials-section {
  text-align: center;
  padding: 60px 20px;
  max-width: 1200px;
  margin: auto;
}

.testimonials-title {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-family: 'Segoe UI', sans-serif;
  color: white;
}

.testimonials-subtitle {
  font-size: 1.1rem;
  color:wheat;
  margin-bottom: 50px;
}

.testimonials-grid {
  display: flex;
  justify-content: space-around;
  gap: 30px;
  flex-wrap: wrap;
}

.testimonial-card {
  flex: 1 1 300px;
  max-width: 340px;
  background: #111827;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease;
}

.testimonial-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 15px;
}

.testimonial-card h3 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  font-weight: 600;
  color: #fff;
}

.testimonial-card p {
  font-size: 0.95rem;
  color: whitesmoke;
  line-height: 1.5;
}

.testimonial-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0,0,0,0.1);
}

.add-item-btn {
  margin-top: 40px;
}

.add-item-btn button {
  background: #d47508;
  color: #fff;
  padding: 10px 22px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.add-item-btn button:hover {
  background: #444;
}
