@import url('https://fonts.googleapis.com/css?family=Sofia&display=swap');
.hero-section {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 500px;
}

.overlay {
  
  height: 100%;
  padding: 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
}


.hero-subtitle {
  font-size: 15px;
  max-width: 600px;
  margin: 20px auto;
  text-align: center;
  color: #fff;
}

.hero-title {
  font-size: 42px;
  font-weight:800;
  text-align: center;
  margin-bottom: 20px;
  color: antiquewhite;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
.btn-primary {
 text-decoration: none;
  margin-left: 500px;

    background: linear-gradient(135deg, #051aa3, #010307);
  color: #fff;
  font-size: 1.1rem;
  padding: 16px 50px;
  border: none;
border-radius: 70px 70px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
}
.body{
  background-color: black;
}